<template>
  <div class="mobile-login-container">
    <!-- Logo -->
    <div class="login-header">
      <div class="login-logo">
        <img 
          :src="$store.state.appInfo.site_logo || '/static/image/uacPoGJlb02AMGnUAAAYLvRuglw960.png'" 
          alt="Logo" 
          class="logo-img"
          @error="handleLogoError"
        />
      </div>
    </div>

    <!--  -->
    <div class="login-form-wrapper">
      <div class="login-form">
        <!--  -->
        <div class="input-group">
          <input 
            v-model="formData.name" 
            type="text" 
            placeholder="" 
            class="login-input"
            maxlength="32"
            autocomplete="off"
          />
        </div>
        
        <!--  -->
        <div class="input-group password-group">
          <input 
            v-model="formData.password" 
            :type="psw1 ? 'password' : 'text'" 
            placeholder="" 
            class="login-input"
            maxlength="32"
            autocomplete="off"
          />
          <div class="password-toggle" @click="changPsw('psw1')">
            <span v-if="psw1"></span>
            <span v-else></span>
          </div>
        </div>

        <!--  -->
        <div class="input-group password-group" v-if="!isLogin">
          <input 
            v-model="formData.confirmPass" 
            :type="psw2 ? 'password' : 'text'" 
            placeholder="" 
            class="login-input"
            maxlength="32"
            autocomplete="off"
          />
          <div class="password-toggle" @click="changPsw('psw2')">
            <span v-if="psw2"></span>
            <span v-else></span>
          </div>
        </div>

        <!--  -->
        <div class="login-options" v-if="isLogin">
          <label class="remember-password">
            <input type="checkbox" v-model="rememberPassword" />
            <span></span>
          </label>
          <a href="#" class="forgot-password" @click.prevent="handleForgotPassword">?</a>
        </div>

        <!--  -->
        <div class="agree-terms" v-if="!isLogin">
          <label class="agree-checkbox">
            <input type="checkbox" v-model="agreeTerms" />
            <span class="checkbox-custom"></span>
          </label>
          <span class="agree-text">
            
            <a href="#" class="terms-link" @click.prevent="handleTermsClick"></a>
            
            <a href="#" class="terms-link" @click.prevent="handlePrivacyClick"></a>
          </span>
        </div>

        <!-- / -->
        <button class="login-btn" @click="submitForm">
          {{ isLogin ? '' : '' }}
        </button>
      </div>
    </div>
    <!--  -->
    <div class="bottom-actions">
      <button class="action-btn register-btn" @click="toggleLoginMode">
        <img src="/static/image/diy/login_btn1.png" :alt="isLogin ? '' : ''" class="btn-image" />
        <span class="btn-text">{{ isLogin ? '' : '' }}</span>
      </button>
      <button class="action-btn guest-btn" @click="$parent.goNav('/')">
        <img src="/static/image/diy/login_btn2.png" alt="" class="btn-image" />
        <span class="btn-text"></span>
      </button>
      <button class="action-btn service-btn" @click="$parent.openKefu">
        <img src="/static/image/diy/login_btn3.png" alt="" class="btn-image" />
        <span class="btn-text"></span>
      </button>
    </div>
    
    <!--  -->
    <ClickCaptcha
      :visible="showCaptcha"
      title=""
      brand=""
      :pointCount="3"
      :generateUrl="generateUrl"
      :verifyUrl="verifyUrl"
      @success="onCaptchaSuccess"
      @close="showCaptcha = false"
    />
    
    <!--  -->
    <van-dialog
      v-model="showForgotPasswordDialog"
      title=""
      :show-cancel-button="true"
      :show-confirm-button="true"
      confirm-button-text=""
      cancel-button-text=""
      @confirm="handleContactService"
      @cancel="handleCancelForgotPassword"
    >
      <div class="forgot-password-content">
        
      </div>
    </van-dialog>
  </div>
</template>

<script>
import ClickCaptcha from '@/components/libs/ClickCaptcha.vue'
export default {
  name: 'login',
  components: { ClickCaptcha },
  data() {
    return {
      showCaptcha: false,
      generateUrl: '/api/captcha/generate',
      verifyUrl: '/api/captcha/verify',
      formData: {
        name: '',
        password: '',
        confirmPass: '',
        realname: '',
        paypassword: '',
      },
      isLogin: true,
      psw1: true,
      psw2: true,
      psw3: true,
      pid: '',
      rememberPassword: false,
      agreeTerms: false,
      showForgotPasswordDialog: false, // 
    };
  },
  created() {
    let that = this;
    var query = that.$route.query;
    if (query.type) {
      that.isLogin = query.type === '0';
    }
    if (query.pid) {
      that.pid = query.pid;
    }
  },
  methods: {
    changPsw(name) {
      this[name] = !this[name];
    },
    toggleLoginMode() {
      // /
      this.isLogin = !this.isLogin;
      // 
      if (this.isLogin) {
        this.formData.confirmPass = '';
      }
    },
    handleForgotPassword() {
      // 
      this.showForgotPasswordDialog = true;
    },
    handleContactService() {
      // 
      this.showForgotPasswordDialog = false;
      this.$parent.goNav('/kefu');
    },
    handleCancelForgotPassword() {
      // 
      this.showForgotPasswordDialog = false;
    },
    handleTermsClick() {
      // 
      this.$parent.showTost(0, '');
    },
    handlePrivacyClick() {
      // 
      this.$parent.showTost(0, '');
    },
    handleLogoError(e) {
      // Logo
      console.error('Logo:', e.target.src);
      // logo
      e.target.style.display = 'none';
    },
    onCaptchaSuccess(sessionId) {
      if (this.isLogin) {
        this.doLogin(sessionId);
      } else {
        this.doRegister(sessionId);
      }
    },
    submitForm() {
      let that = this;
      let info = that.formData;
      
      if (!info.name || !info.password) {
        that.$parent.showTost(0, '');
        return;
      }
      
      // 
      if (!that.isLogin) {
        if (!info.confirmPass) {
          that.$parent.showTost(0, '');
          return;
        }
        if (info.password !== info.confirmPass) {
          that.$parent.showTost(0, '');
          return;
        }
        if (!that.agreeTerms) {
          that.$parent.showTost(0, '');
          return;
        }
      }
      
      // 
      this.showCaptcha = true;
    },
    doRegister(sessionId) {
      let that = this;
      let info = that.formData;
      
      if (sessionId) {
        info.captcha_id = sessionId;
      }

      that.$parent.showLoading();
      if (that.pid) {
        info.pid = that.pid;
      }
      
      that.$apiFun.register(info).then(res => {
        that.$parent.showTost(1, res.message);
        if (res.code == 200) {
          sessionStorage.setItem('token', res.data.api_token);
          that.$store.commit('changToken');
          that.$parent.getUserInfo();
          that.$parent.openDaoTime();
          that.$parent.goNav('/');
        }
        that.$parent.hideLoading();
      });
    },
    doLogin(sessionId) {
      let that = this;
      let info = {
        name: that.formData.name,
        password: that.formData.password
      };
      
      if (sessionId) {
        info.captcha_id = sessionId;
      }

      that.$parent.showLoading();
      that.$apiFun.login(info).then(res => {
        if (res.code !== 200) {
          that.$parent.showTost(0, res.message);
          that.$parent.hideLoading();
        }
        if (res.code === 200) {
          sessionStorage.setItem('token', res.data.api_token);
          that.$store.commit('changToken');
          that.$parent.getUserInfo();
          that.$parent.openDaoTime();
          that.$parent.goNav('/');
        }
        that.$parent.hideLoading();
      });
    },
  },
  mounted() {
    // this.refresh();
  }
};
</script>

<style lang="scss" scoped>
.mobile-login-container {
  width: 100vw;
  min-height: 100vh;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 50%, #0f3460 100%);
  background-image: url('/static/image/diy/login_bg.jpg');
  background-size: cover;
  background-position: center;
  background-blend-mode: overlay;
  padding: 60px 15px 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

// Logo
.login-header {
  text-align: center;
  padding: 30px 0 40px;
  
  .login-logo {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 12px;
    
    .logo-img {
      width: 60%;
      display: block;
      object-fit: contain;
    }
  }
}

// 
.login-form-wrapper {
  margin-bottom: 0.5rem;
}

.login-form {
  .input-group {
    position: relative;
    margin-bottom: 15px;
    
    .login-input {
      width: 100%;
      height: 50px;
      padding: 0 15px;
      background: rgba(200, 200, 200, 0.3);
      border: 1px solid rgba(255, 255, 255, 0.3);
      border-radius: 12px;
      font-size: 16px;
      color: #ffffff;
      box-sizing: border-box;
      
      &::placeholder {
        color: rgba(255, 255, 255, 0.6);
      }
      
      &:focus {
        outline: none;
        border-color: #1890ff;
        background: rgba(200, 200, 200, 0.4);
      }
    }
    
    &.password-group {
      .password-toggle {
        position: absolute;
        right: 15px;
        top: 50%;
        transform: translateY(-50%);
        cursor: pointer;
        font-size: 20px;
      }
    }
  }
  
  .login-options {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 50px;
    
    .remember-password {
      display: flex;
      align-items: center;
      gap: 8px;
      color: #ffffff;
      font-size: 14px;
      cursor: pointer;
      
      input[type="checkbox"] {
        width: 18px;
        height: 18px;
        accent-color: #1890ff;
        cursor: pointer;
      }
    }
    
    .forgot-password {
      color: #ffffff;
      font-size: 14px;
      text-decoration: none;
      
      &:hover {
        text-decoration: underline;
      }
    }
  }
  
  .agree-terms {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    margin-bottom: 20px;
    
    .agree-checkbox {
      position: relative;
      display: inline-block;
      cursor: pointer;
      
      input[type="checkbox"] {
        position: absolute;
        opacity: 0;
        width: 0;
        height: 0;
        
        &:checked + .checkbox-custom {
          background: #1890ff;
          border-color: #1890ff;
          
          &::after {
            display: block;
          }
        }
      }
      
      .checkbox-custom {
        display: inline-block;
        width: 18px;
        height: 18px;
        border: 2px solid rgba(255, 255, 255, 0.5);
        border-radius: 4px;
        background: transparent;
        position: relative;
        flex-shrink: 0;
        
        &::after {
          content: '';
          position: absolute;
          left: 5px;
          top: 2px;
          width: 4px;
          height: 8px;
          border: solid white;
          border-width: 0 2px 2px 0;
          transform: rotate(45deg);
          display: none;
        }
      }
    }
    
    .agree-text {
      color: rgba(255, 255, 255, 0.9);
      font-size: 12px;
      line-height: 1.5;
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      gap: 2px;
      
      .terms-link {
        color: #1890ff;
        text-decoration: none;
        font-size: 12px;
        
        &:hover {
          text-decoration: underline;
        }
      }
    }
  }
  
  .login-btn {
    width: 100%;
    height: 50px;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    border: none;
    border-radius: 12px;
    color: #ffffff;
    font-size: 18px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    
    &:active {
      transform: scale(0.98);
      opacity: 0.9;
    }
  }
  
}

// 
.bottom-actions {
  display: flex;
  gap: 10px;
  padding: 0 10px;
  
  .action-btn {
    flex: 1;
    height: auto;
    background: rgba(255, 255, 255, 0.1);
    border: none;
    border-radius: 12px;
    padding: 15px 10px;
    cursor: pointer;
    transition: all 0.3s ease;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 8px;
    
    .btn-image {
      width: 30%;
      max-width: 35px;
      height: auto;
      display: block;
    }
    
    .btn-text {
      color: rgba(255, 255, 255, 0.9);
      font-size: 12px;
      text-align: center;
    }
    
    &:active {
      transform: scale(0.98);
      opacity: 0.9;
      background: rgba(255, 255, 255, 0.15);
    }
  }
}

// 
::v-deep .van-dialog {
  background: linear-gradient(to bottom, #9d4edd 0%, rgba(157, 78, 221, 0.5) 50%, rgba(157, 78, 221, 0.35) 100%) !important;
  border-radius: 12px;
  overflow: hidden;
  
  .van-dialog__header {
    padding: 15px 20px !important;
    background: transparent !important;
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid #74249b !important;
    
    .van-dialog__title {
      color: #ffffff !important;
      font-weight: bold !important;
      font-size: 18px;
    }
  }
  
  .van-dialog__content {
    padding: 10px 20px 20px;
    background: transparent !important;
  }
  
  .van-dialog__footer {
    border-top: 1px solid rgba(255, 255, 255, 0.2);
    background: #7534a9 !important;
    display: flex;
  }
  
  .van-dialog__confirm,
  .van-dialog__cancel {
    color: #ffffff !important;
    background-color: transparent !important;
    flex: 1;
  }
  
  .van-dialog__confirm {
    background-color: rgba(255, 255, 255, 0.2) !important;
    border-left: 1px solid #660a93 !important;
  }
  
  .van-dialog__cancel {
    background-color: transparent !important;
  }
  
  .van-button {
    background-color: transparent !important;
  }
  
  .van-button--default {
    background-color: transparent !important;
  }
}

.forgot-password-content {
  padding: 20px 0;
  text-align: center;
  font-size: 14px;
  color: #ffffff;
  line-height: 1.5;
}
</style>



