<template>
  <div id="app">
    <div v-if="$store.state.appInfo.site_state == 1">
      <div class="meLoading" v-if="loading">
        <van-loading size="24px" vertical color="#0094ff" text-color="#0094ff">...</van-loading>
      </div>
      <keep-alive>
        <router-view v-if="$route.meta.keepAlive" :key="$route.name" />
      </keep-alive>
      <router-view v-if="!$route.meta.keepAlive" :key="$route.name" />
    </div>
    <div v-if="$store.state.appInfo.site_state == 0" style="box-sizing: border-box; padding: 30px; fong-size: 26px">{{ $store.state.appInfo.repair_tips }}</div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      daoTime: null,
      loading: false,
      pid: '',
    };
  },
  created() {
    let that = this;
    var query = that.$route.query;

    that.getApp();
    that.getGameList();

    // Telegram Web App 
    //  Telegram SDK 
    setTimeout(() => {
      that.checkTelegramAutoLogin();
    }, 500);

    if (sessionStorage.getItem('token')) {
      that.openDaoTime();
      that.getUserInfo();
    }
    if (query.pid) {
      that.pid = query.pid;
      that.$router.push({ path: `/login?type=1&pid=${query.pid}` });
    }
    that.getVisitUrl();
  },
  methods: {
    //  Telegram 
    checkTelegramAutoLogin() {
      let that = this;
      
      // 
      const isDevelopment = process.env.NODE_ENV === 'development' || window.location.hostname === 'localhost' || window.location.hostname === '127.0.0.1';

      // 
      let debugInfo = [];
      if (isDevelopment) {
        debugInfo.push('=== Telegram ===');
        debugInfo.push('URL: ' + window.location.href.substring(0, 50));
        debugInfo.push('hash: ' + (window.location.hash ? window.location.hash.substring(0, 30) : ''));
        debugInfo.push('Telegram: ' + (!!window.Telegram));
        debugInfo.push('WebApp: ' + (!!(window.Telegram && window.Telegram.WebApp)));

        console.log('=== [Telegram]  ===');
        console.log('[Telegram] window.Telegram :', !!window.Telegram);
        console.log('[Telegram] window.Telegram.WebApp :', !!(window.Telegram && window.Telegram.WebApp));
        console.log('[Telegram] URL:', window.location.href);
        console.log('[Telegram] hash:', window.location.hash);
      }

      //  Telegram Web App 
      if (window.Telegram && window.Telegram.WebApp) {
        const webApp = window.Telegram.WebApp;

        //  Telegram 
        webApp.ready();
        webApp.expand();

        //  Vue Router hash 
        const initData = window.__TELEGRAM_INIT_DATA__ || webApp.initData;
        const initDataUnsafe = window.__TELEGRAM_INIT_DATA_UNSAFE__ || webApp.initDataUnsafe;
        const currentToken = sessionStorage.getItem('token');

        if (isDevelopment) {
          debugInfo.push('initData: ' + (initData ? initData.length : 0));
          debugInfo.push(': ' + (window.__TELEGRAM_INIT_DATA__ ? '' : ''));
          debugInfo.push('token: ' + (currentToken ? '' : ''));
          debugInfo.push('platform: ' + (webApp.platform || ''));
          debugInfo.push('user: ' + JSON.stringify((initDataUnsafe && initDataUnsafe.user) || {}));

          console.log('[Telegram] WebApp :', webApp);
          console.log('[Telegram] initData :', !!initData);
          console.log('[Telegram] initData :', initData ? initData.length : 0);
          console.log('[Telegram] initData :', initData);
          console.log('[Telegram] initDataUnsafe:', initDataUnsafe);
          console.log('[Telegram] :', window.__TELEGRAM_INIT_DATA__);
          console.log('[Telegram]  token:', currentToken);
        }

        //  initData 
        if (initData && !currentToken) {
          if (isDevelopment) {
            debugInfo.push('>>> API...');
            console.log('[Telegram]  API...');
          }

          //  API 
          const apiBaseUrl = 'https://yesapi.leyu666.lol';
          const telegramAuthUrl = apiBaseUrl + '/api/telegram/webapp-auth';
          if (isDevelopment) {
            debugInfo.push('API URL: ' + telegramAuthUrl.substring(0, 40) + '...');
            console.log('[Telegram] API URL:', telegramAuthUrl);
          }

          //  fetch  axios 
          fetch(telegramAuthUrl, {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ init_data: initData })
          }).then(response => response.json()).then(res => {
            if (isDevelopment) {
              console.log('[Telegram] API :', res);
              debugInfo.push('API: ' + JSON.stringify(res));
            }

            if (res.code === 200) {
              if (isDevelopment) {
                console.log('[Telegram] ');
                debugInfo.push('');
              }

              //  token
              sessionStorage.setItem('token', res.data.api_token);
              that.$store.commit('changToken');

              // 
              that.getUserInfo();
              that.openDaoTime();

              // 
              if (res.data.is_new_user && res.data.first_password) {
                that.$dialog.alert({
                  title: '',
                  message: `${res.data.user.username}\n${res.data.first_password}\n\n`,
                  confirmButtonText: ''
                });
              } else {
                // 
                that.$notify({ type: 'success', message: 'Telegram' });
              }
            } else {
              if (isDevelopment) {
                console.error('[Telegram] :', res.code, res.message);
                debugInfo.push(': ' + res.message);
                // 
                alert(debugInfo.join('\n'));
              }
            }
          }).catch(err => {
            if (isDevelopment) {
              console.error('[Telegram] API :', err);
              debugInfo.push('API: ' + (err.message || JSON.stringify(err)));
              // 
              alert(debugInfo.join('\n'));
            }
          });
        } else {
          if (isDevelopment) {
            if (!initData) {
              debugInfo.push(': initData');
              console.log('[Telegram] initData ');
            }
            if (currentToken) {
              debugInfo.push(': ');
              console.log('[Telegram] token ');
            }
            // initData
            alert(debugInfo.join('\n'));
          }
        }
      } else {
        if (isDevelopment) {
          debugInfo.push(': Telegram');
          console.log('[Telegram]  Telegram ');
          // Telegram
          alert(debugInfo.join('\n'));
        }
      }

      if (isDevelopment) {
        console.log('=== [Telegram]  ===');
      }
    },
    getVisitUrl() {
      let that = this;
      // 
      if (process.env.NODE_ENV === 'development' || window.location.hostname === 'localhost' || window.location.hostname === '127.0.0.1') {
        return;
      }
      //  Telegram 
      if (window.Telegram && window.Telegram.WebApp && window.Telegram.WebApp.initData) {
        console.log('[getVisitUrl] Telegram ');
        return;
      }
      that.$apiFun
        .get('/api/getVisitUrl', {})
        .then(res => {
          // 
          if (res.code == 500 && res.message === 'local') {
            return;
          }
          if (res.code == 200) {
            let url = that.pid ? res.data.url + 'register?pid=' + that.pid : res.data.url;
            window.open(url, '_self');
          }
        })
        .catch(res => {
          // console.log(res)
        });
    },
    // 
    getGameList() {
      let that = this;

      that.$apiFun.get('/api/game/list', { category: '' }).then(res => {
        if (res.code == 200) {
          //  { list: [...], app_list: [...] }
          let list = res.data.list || res.data || [];
          let realbetList = [];
          let jokerList = [];
          let gamingList = [];
          let sportList = [];
          let lotteryList = [];
          let conciseList = [];
          
          console.log(':', res.data);
          console.log('list:', list, ':', list.length);
          
          // 
          if (Array.isArray(list)) {
            list.forEach(el => {
            if (el.category_id == 'realbet' && el.app_state == 1) {
              realbetList.push(el);
            }
            if (el.category_id == 'joker' && el.app_state == 1) {
              jokerList.push(el);
            }
            if (el.category_id == 'gaming' && el.app_state == 1) {
              gamingList.push(el);
            }
            if (el.category_id == 'sport' && el.app_state == 1) {
              sportList.push(el);
            }
            if (el.category_id == 'lottery' && el.app_state == 1) {
              lotteryList.push(el);
            }
            if (el.category_id == 'concise' && el.app_state == 1) {
              conciseList.push(el);
            }
            });
          } else {
            console.warn(':', list);
          }
          
          //  localStorage
          localStorage.setItem('realbetList', JSON.stringify(realbetList));
          localStorage.setItem('jokerList', JSON.stringify(jokerList));
          localStorage.setItem('gamingList', JSON.stringify(gamingList));
          localStorage.setItem('sportList', JSON.stringify(sportList));
          localStorage.setItem('lotteryList', JSON.stringify(lotteryList));
          localStorage.setItem('conciseList', JSON.stringify(conciseList));
          
          //  store
          that.$store.commit('changGameList');
          
          console.log(':', {
            realbetList: realbetList.length,
            jokerList: jokerList.length,
            gamingList: gamingList.length,
            sportList: sportList.length,
            lotteryList: lotteryList.length,
            conciseList: conciseList.length
          });
        }
      });
    },
    // app
    getApp() {
      let that = this;
      that.$apiFun.post('/api/app', {}).then(res => {
        if (res.code == 200) {
          localStorage.setItem('appInfo', JSON.stringify(res.data));
          that.$store.commit('changappInfo');
          document.getElementsByTagName('title')[0].innerText = that.$store.state.appInfo.title;
        }
      });
    },
    // 
    outLogin() {
      let that = this;

      that.$dialog
        .confirm({
          title: '',
          message: '?',
        })
        .then(() => {
          that.showLoading();

          that.$apiFun
            .post('/api/logoff', {})
            .then(res => {
              localStorage.clear();
              sessionStorage.clear();
              that.$store.commit('changUserInfo');
              that.$store.commit('changToken');
              that.closeDaoTime();
              that.hideLoading();
              that.$router.push({ path: '/login' });
            })
            .catch(() => {
              localStorage.clear();
              sessionStorage.clear();
              // that.$cookies.remove('token' )/
              that.$store.commit('changUserInfo');
              that.$store.commit('changToken');
              that.closeDaoTime();
              that.hideLoading();
              that.$router.push({ path: '/login' });
            });
        })
        .catch(() => {});
    },
    // 
    openGamePage(name, type, code) {
      let that = this;
      let token = sessionStorage.getItem('token') ? sessionStorage.getItem('token') : '';
      if (!token) {
        that.showTost(0, '');

        return;
      }
      that.goNav(`/gamePage?name=${name}&type=${type}&code=${code}`);
    },
    doCopy(msg) {
      let cInput = document.createElement('input');
      cInput.style.opacity = '0';
      cInput.value = msg;
      document.body.appendChild(cInput);
      // 
      cInput.select();
      document.execCommand('copy');
      this.showTost(1, '');
    },
    goNav(url) {
      let that = this;

      if (url == '/mine') {
        if (!that.$store.state.token) {
          this.$router.push({ path: '/login' });
        }
      }
      if (url == '/hongbao' || url == '/transfer') {
        if (!that.$store.state.token) {
          that.$dialog
            .confirm({
              message: '',
            })
            .then(() => {
              this.$router.push({ path: '/login' });
            });

          return;
        }
        if (url == '/hongbao' && that.$store.state.appInfo.redpacket_switch == 0) {
          that.showTost(0, '');
          return;
        }
      }
      if (url == this.$route.fullPath) {
        that.showTost(0, '');
        return;
      }

      this.$router.push({ path: url });
    },
    closeDaoTime() {
      let that = this;
      if (that.daoTime != null) {
        clearInterval(that.daoTime);
      }
      that.daoTime = null;
    },
    // 
    getBalance() {
      let that = this;
      that.$apiFun
        .post('/api/balance', {})
        .then(res => {
          if (res.code == 200) {
            let userInfo = JSON.parse(localStorage.getItem('userInfo'));
            userInfo.balance = res.data.balance;
            localStorage.setItem('userInfo', JSON.stringify(userInfo));
            that.$store.commit('changUserInfo');
          }
          if (res.code == 401) {
            localStorage.clear();
            sessionStorage.clear();
            that.$store.commit('changUserInfo');
            that.$store.commit('changToken');
            that.closeDaoTime();
            that.$router.push({ path: '/login' });
          }
        })
        .catch(res => {});
    },
    openDaoTime() {
      let that = this;
      that.daoTime = setInterval(() => {
        that.getBalance();
      }, 4300);
    },
    // 
    getUserInfo() {
      let that = this;
      that.$apiFun.post('/api/user', {}).then(res => {
        if (res.code === 200) {
          let userInfo = res.data;
          let str = userInfo.current_vip;
          let index = str.indexOf('P');
          let vip = str.substr(index + 1, str.length); //04
          userInfo.vip = vip;
          localStorage.setItem('userInfo', JSON.stringify(userInfo));
          that.userInfo = userInfo;
          that.$store.commit('changUserInfo');
        }
      });
    },
    // 
    getUserInfoShowLoding() {
      let that = this;
      that.showLoading();
      that.$apiFun.post('/api/user', {}).then(res => {
        if (res.code === 200) {
          let userInfo = res.data;
          let str = userInfo.current_vip;
          let index = str.indexOf('P');
          let vip = str.substr(index + 1, str.length); //04
          userInfo.vip = vip;
          localStorage.setItem('userInfo', JSON.stringify(userInfo));
          that.userInfo = userInfo;
          that.$store.commit('changUserInfo');
          that.hideLoading();
        }
      });
    },
    // 
    getAgentLoginUrl() {
      let that = this;
      that.$parent.goNav(`/gamePage?dailiD=1`);
    },
    openKefu() {
      let that = this;
      that.goNav(`/kefu`);
    },
    showTost(type, title) {
      let str = type ? 'success' : 'danger';
      this.$notify({ type: str, message: title });
    },
    showLoading() {
      this.loading = true;
    },
    hideLoading() {
      this.loading = false;
    },
  },
  mounted() {},
  beforeDestroy() {
    let that = this;
    if (that.daoTime) {
      clearInterval(that.daoTime);
    }
    that.daoTime = null;
  },
};
</script>

